import React, { useState } from 'react';
import {BrowserRouter as Router, Link} from "react-router-dom"
import './Form.css';

const FormSignup = () => {
  let [city,setCity] = useState("");
  let [require,setRequire] = useState("");
  let [verify,setVerify] = useState("");
  let [likes, setLikes] = useState("0");
  let [needed,setNeeded] = useState("");
  let [nearme,setNearme] = useState("");
  let [today,setToday] = useState("");

  const onSubmitHandler = (e) => {
    e.preventDefault();
    window.location.href = "https://twitter.com/search?q=min_faves%3A"+likes+"%20"+require+"%20"+city+"%20"+verify+"%20"+needed+"%20since%3A"+today+"&src=typed_query"+nearme;
  }
  let aaj = new Date(),

  date = aaj.getFullYear() + '-0' + (aaj.getMonth() + 1) + '-' + aaj.getDate();
  console.log(date)

  
  return (
    <div className='form-content-right'>
       
       <form className="form" 
       onSubmit={onSubmitHandler}>
       <h1>
          Enter your city and requirement and the Webapp will show you a list of posts that might help you from twitter  
        </h1>
       <h1>
          In case of issues or suggestions, text me on ig - @its.apratim 
        </h1>
        <div className='form-inputs'>
          <label className='form-label'>Enter your City</label>
          <input
            className='form-input'
            type='text'
            placeholder='Enter your City'
            onChange={(e) => setCity(e.target.value)}
            checked
          />
        </div>
        <div className='form-inputs'>
          <label className='form-label'>What do you require</label>
          <input
            className='form-input'
            type='text'
            placeholder='Beds/ Oxygen/ Ventilator/ Ramdesivir/ Tifin'
            onChange={(e) => setRequire(e.target.value)}
          />
        </div>
        <div className='form-inputs'>
          <label className='form-label'>Minimum number of likes on the post</label>
          <input
            className='form-input'
            type='number'
            placeholder='Enter 0 for most cases'
            onChange={(e) => setLikes(e.target.value)}
            checked
          />
        </div>
        <div className='form-inputs'>
          <label className='form-label'>Only posts claiming to be verified: </label>
          <input
            type="checkbox"
            onChange={(e) => setVerify("verified")}
          />
        </div>
        <div className='form-inputs'>
          <label className='form-label'>Hide posts with "Needed/ Required": </label>
          <input
            type="checkbox"
            onChange={(e) => setNeeded("-needed%20-required")}
          />
        </div>
        <div className='form-inputs'>
          <label className='form-label'>Only posts near you: </label>
          <input
            type="checkbox"
            onChange={(e) => setNearme("&lf=on")}
          />
        </div>
        <div className='form-inputs'>
          <label className='form-label'>Only posts from today: </label>
          <input
            type="checkbox"
            onChange={(e) => setToday(date)}
          />
        </div>
        <button 
          type = "submit"
          className='form-input-btn' >
          Search
        </button>
       
      
      </form>

      
    </div>
)
  }

export default FormSignup;
